@tailwind base;
@tailwind components;
@tailwind utilities;
.animation-arrow-left img, .animation-arrow-left svg {
  @apply transition; }

.animation-arrow-left:hover img, .animation-arrow-left:hover svg {
  transform: translateX(-3px); }

.btn {
  @apply bg-transparent text-white transition min-w-full rounded-full font-bold h-15 relative overflow-hidden;
  z-index: 1; }
  .btn::before {
    content: "";
    z-index: -2;
    transition: opacity -1s cubic-bezier(0.23, 1, 0.32, 1);
    @apply block bg-red rounded-full border-2 border-red bottom-0 left-0 absolute right-0 top-0; }
  .btn::after {
    content: "";
    z-index: -1;
    transform: scale3d(0.7, 1, 1);
    transition: transform 0.3s cubic-bezier(0.23, 1, 0.32, 1), opacity 0.2s cubic-bezier(0.23, 1, 0.32, 1), border 0.2s cubic-bezier(0.23, 1, 0.32, 1), background-color 0.2s cubic-bezier(0.23, 1, 0.32, 1);
    @apply block bg-pink rounded-full border-2 border-pink bottom-0 left-0 absolute right-0 top-0 opacity-0; }
  .btn:hover::before {
    transition: opacity 0.2s cubic-bezier(0.23, 1, 0.32, 1) 0.15s;
    @apply opacity-0; }
  .btn:hover::after {
    transform: scale3d(1, 1, 1);
    @apply opacity-100; }
  .btn:active::after {
    @apply bg-dark-red border-dark-red; }
  .btn:focus {
    @apply outline-none; }
  .btn-sm {
    @apply h-12; }
  .btn-xs {
    @apply h-10; }
  .btn-outline {
    @apply text-black; }
    .btn-outline::before {
      @apply bg-transparent; }
    .btn-outline:hover {
      @apply text-white; }

.form-group {
  @apply mb-4; }
  .form-group label {
    @apply block text-grey text-sm pl-5;
    margin-bottom: 0.3rem; }
  .form-group.error label {
    @apply text-red; }
  .form-group.error .form-control {
    @apply border-red; }
    .form-group.error .form-control:focus {
      @apply border-dark-red; }

.form-control {
  @apply block bg-white rounded-full border min-w-full border-light-grey text-lg leading-none transition px-5 h-13 pt-3;
  padding-bottom: 0.9rem; }
  .form-control:focus {
    @apply outline-none border-black; }

input[type='checkbox'] {
  display: none; }

input[type='checkbox'] + label {
  @apply relative flex items-center text-black cursor-pointer text-sm;
  transition: color 250ms cubic-bezier(0.4, 0, 0.23, 1); }

input[type='checkbox'] + label > span {
  @apply flex justify-center items-center bg-transparent border-2 border-red rounded cursor-pointer;
  width: 26px;
  height: 26px;
  transition: all 250ms cubic-bezier(0.4, 0, 0.23, 1);
  margin-right: 9px; }

input[type='checkbox'] + label:hover, input[type='checkbox']:focus + label {
  color: #555; }

input[type='checkbox'] + label:hover > span, input[type='checkbox']:focus + label > span {
  background: rgba(255, 255, 255, 0.1); }

input[type='checkbox']:checked + label > span {
  border: 13px solid;
  @apply border-red;
  animation: shrink-bounce 200ms cubic-bezier(0.4, 0, 0.23, 1); }

input[type='checkbox']:checked + label > span:before {
  content: "";
  position: absolute;
  top: 12px;
  left: 7px;
  border-right: 2px solid transparent;
  border-bottom: 2px solid transparent;
  transform: rotate(45deg);
  transform-origin: 0% 100%;
  animation: checkbox-check 125ms 250ms cubic-bezier(0.4, 0, 0.23, 1) forwards;
  box-sizing: content-box; }

@keyframes shrink-bounce {
  0% {
    transform: scale(1); }
  33% {
    transform: scale(0.85); }
  100% {
    transform: scale(1); } }

@keyframes checkbox-check {
  0% {
    width: 0;
    height: 0;
    @apply border-white;
    transform: translate3d(0, 0, 0) rotate(45deg); }
  33% {
    width: 5px;
    height: 0;
    transform: translate3d(0, 0, 0) rotate(45deg); }
  100% {
    width: 5px;
    height: 12px;
    @apply border-white;
    transform: translate3d(0, -12px, 0) rotate(45deg); } }

.group-radio {
  @apply flex bg-white rounded-full;
  padding: 5px; }
  .group-radio input[type='radio'] {
    display: none; }
    .group-radio input[type='radio'] + label {
      @apply text-xs text-black rounded-full bg-white cursor-pointer px-5 transition font-medium;
      line-height: 40px;
      height: 40px; }
      .group-radio input[type='radio'] + label:hover {
        @apply text-red; }
    .group-radio input[type='radio']:checked + label {
      @apply bg-red text-white; }

@screen md {
  .group-radio input[type='radio'] {
    @apply text-lg; } }

.animation-background {
  z-index: -1;
  @apply fixed top-0 left-0 w-full h-screen; }
  .animation-background .animation-gradient {
    height: 57.6%;
    @apply block w-full relative; }
    .animation-background .animation-gradient_full {
      @apply h-full; }
  .animation-background__angle {
    @apply absolute left-0 bottom-0 w-full z-10;
    margin-bottom: -1px; }
  .animation-background__footer {
    height: 42.4%;
    @apply block w-full bg-xs-grey; }

.animation-gradient {
  background: linear-gradient(45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradientBG 15s ease infinite; }

@keyframes gradientBG {
  0% {
    background-position: 0 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0 50%; } }

.modal {
  @apply flex justify-center items-center; }
  .modal__dialog {
    @apply w-full px-1; }
  .modal__logo {
    @apply my-4; }
    .modal__logo img {
      margin-left: -30px; }

@screen sm {
  .modal {
    @apply h-screen; }
    .modal__dialog {
      width: 410px; }
    .modal__logo {
      margin-bottom: 62px; } }
  .modal__content {
    @apply bg-white;
    border-radius: 10px; }
    .modal__content__header {
      @apply border-b border-divider flex justify-between items-center;
      padding: 30px 30px 28px; }
    .modal__content__body {
      @apply relative z-10;
      padding: 0 30px 30px; }

.plans {
  @apply flex justify-center flex-col; }
  .plans__item {
    @apply bg-white rounded-lg pt-5 pb-8 mx-4 mb-4; }
    .plans__item .plans__body {
      @apply px-4; }

@screen md {
  .plans {
    @apply flex-row; }
    .plans__item {
      width: 31.5%; }
      .plans__item:nth-child(odd) {
        width: 30%;
        @apply my-4 mx-6; }
        .plans__item:nth-child(odd) .plans__body {
          @apply px-7; }
      .plans__item:nth-child(even) {
        @apply py-9; }
        .plans__item:nth-child(even) .plans__body {
          @apply px-9; } }
